.page {
  scroll-behavior: smooth;

  &__body {
    margin: 0;

    &--with-menu {
      overflow: hidden;
    }
  }

  &__section {
    &:nth-child(2) {
      margin-bottom: 150px;
    }

    @include onTablet {
      &:nth-child(2) {
        margin-bottom: 0px;
      }
    }
  }

  &__section-title {
    @extend %titleMobileStyle;

    width: 280px;
    margin: 0 auto 40px;

    @include onTablet {
      width: 100%;
      text-align: left;
      font-size: 48px;
      margin-bottom: 48px;
    }

    @include onDesktop {
      margin-bottom: 56px;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: transform 1s;
    transform: translateX(-100%);

    &:target {
      transform: translateX(0);
    }
  }
}
